.litlem__navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem 8rem;
    background-color: #fff;
}

.litlem__navbar-links {
    flex: 1;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}

.litlem__navbar-links_logo {
    margin-right: 2rem;
}

.litlem__navbar-links_logo img {
    width: 180px;
    height: auto;
}

.litlem__navbar-links_container {
    display: flex;
    flex-direction: row;
}

.litlem__navbar-sign {
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.litlem__navbar-links_container p,
.litlem__navbar-sign p,
.litlem__navbar-menu_container p {
    color: #495e57;
    font-family: var(--paragraph-font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    text-transform: capitalize;
    margin: 0 1rem;
    cursor: pointer;
}

.litlem__navbar-sign button {
    padding: 0.5rem 1rem;
    color: #fff;
    background-color: #495e57;
    font-family: var(--paragraph-font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border-radius: 5px;
    border: 0;
    outline: none;
    cursor: pointer;
}

.litlem__navbar-menu_container-links-sign button {
    padding: 0.5rem 1rem;
    color: #fff;
    background-color: #495e57;
    font-family: var(--paragraph-font-family);
    font-weight: 500;
    font-size: 18px;
    line-height: 25px;
    border-radius: 5px;
    border: 0;
    outline: none;
    cursor: pointer;
}

.litlem__navbar-menu {
    margin-left: 1rem;

    display: none;
    position: relative;
}

.litlem__navbar-menu svg {
    cursor: pointer;
}

.litlem__navbar-menu_container {
    display: flex;
    justify-content: flex-end;
    align-items: flex-end;
    flex-direction: column;    
    text-align: end;
    background: #fbdabb;
    padding: 2rem;
    position: absolute;
    top: 40px;
    right: 0;
    margin-top: 1rem;
    min-width: 210px;
    border-radius: 5px;
    box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
}

.litlem__navbar-menu_container p {
    margin: 1rem 0;
}

.litlem__navbar-menu_container-links-sign {
    display: none;
}

.litlem__navbar-links_container a {
    position: relative;
  }
  
  .litlem__navbar-links_container a::after {
    content: "";
    position: absolute;
    bottom: -5px;
    left: 0;
    width: 100%;
    height: 3px;
    background-color: #f4ce14;
    border-radius: 10px;
    transform: scaleX(0);
    transition: transform 0.1s ease-in-out;
  }
  
  .litlem__navbar-links_container a:hover::after {
    transform: scaleX(1);
  }

@media screen and (max-width: 1165px) {
    .litlem__navbar-links_container {
        display: none;
    }

    .litlem__navbar-menu {
        display: flex;
    }
}

@media screen and (max-width: 700px) {
    .litlem__navbar {
        padding: 2rem 4rem;
    }

    .litlem__navbar-links_logo img {
        width: 180px;
        height: auto;
    }
}

@media screen and (max-width: 550px) {
    .litlem__navbar {
        padding: 2rem;
    }

    .litlem__navbar-sign {
        display: none;
    }

    .litlem__navbar-menu_container {
        top: 20px;
    }

    .litlem__navbar-menu_container-links-sign {
        display: block;
    }

    .litlem__navbar-links_logo img {
        width: 150px;
        height: auto;
    }
}